<template>
  <form @submit.prevent="submitForm" class="mb-4">
    <input v-model="name" placeholder="Имя" class="p-2 border" required />
    <input v-model="phone" placeholder="Телефон" class="p-2 border" required />
    <input v-model="email" placeholder="Email" class="p-2 border" required />
    <button type="submit" class="bg-green-500 text-white p-2">
      Добавить контакт
    </button>
  </form>
</template>

<script>
import { ref } from "vue";

export default {
  emits: ["addContact"],
  setup(props, { emit }) {
    const name = ref("");
    const phone = ref("");
    const email = ref("");

    const submitForm = () => {
      emit("addContact", {
        name: name.value,
        phone: phone.value,
        email: email.value,
      });
      name.value = "";
      phone.value = "";
      email.value = "";
    };

    return { name, phone, email, submitForm };
  },
};
</script>
